



























import login from '../../components/login.vue'
import ut from '../../utils/index.js';
export default {
	data() {
		return {
			username:"",
			password:""
		}
	},
	onLoad() {

	},
	methods: {
		go_mine_login(){
			wx.navigateTo({
				url: '../mine/login'
			})
		},
		cg_active(){
			console.log(1)
			this.check=!this.check;
		},
		req_login(){
			if(!this.username){
				ut.totast('请输入用户名');
				return;
			}
			
			if(!this.password){
				ut.totast('请输入密码');
				return;
			}
			
			ut.request({
				data: {
					username:this.username,
					password:this.password,
					code:1
				},
				url: "user/login"
			}).then(data=>{
				console.log(data.token)
				wx.setStorageSync('token',data.token)
				wx.navigateBack()
			})
		}
	}
}



